import React from 'react';

const Object_data = {}

var td_array = []

Object_data.th = (value) => {
    td_array.push(
        <th scope="col">{value}</th>
    )
}

Object_data.th_star = () => {
    td_array.push(
        <th scope="row">☆</th>
    )
}

Object_data.td = (value) => {
    td_array.push(
        <td>{value}</td>
    )
}

Object_data.get_array = () => {
    return (
        td_array.map((data, index) => {
            if (td_array.length == index + 1) {
                td_array = []
            }
            return data
        })
    )
}

Object_data.row_data = (array_data) => {
    return (
        <tr>
            {
                Object_data.th_star()
            }
            {
                array_data.map(data => {
                    return Object_data.td(data)
                })
            }

            {
                Object_data.get_array(data => {
                    return data
                })
            }
        </tr>
    )
}

export default Object_data;