import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

class BarCode extends Component {
    constructor(props) {
        super(props);
        this.state = {
            height: null
        }
    }

    async componentDidMount() {
        var height = await document.getElementById('barcode-main-div').offsetHeight
        console.log('height :::::', height);

        this.setState({
            height: height
        })
    }

    get_clip_path = (height) => {
        console.log('h :::::', height);
        
        var h = height + 'rem';
        return (
            <div className="row clip-path-div" style={{ height: height != null ? h : null, backgroundColor: 'yellow' }}>
                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                </div>
            </div>
        )
    }

    render() {
        const { class_h_code, class_tag_code } = this.props;
        const { height } = this.state
        return (
            <>
                <div id="barcode-main-div" className="barcode-main-div" style = {{marginTop: '2rem'}}>
                    <div id='row-fluid' className={class_tag_code.row_my_container_fluid} style={{ height: '10rem' }}>
                        <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12" style={{ zIndex: '2' }}>
                            <h1 className={class_tag_code.display_6} style={{ fontSize: '2rem' }}>{class_h_code.h1}</h1>
                        </div>
                    </div>
                    <div id="details-div" className={class_tag_code.row_details}>
                        <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12" style={{ height: '10rem', zIndex: '2' }}>
                            <h1 className="" style={{ fontSize: '1.4rem' }}>{class_h_code.h2} </h1>

                        </div>
                    </div>
                </div>

                {/* -------------------------------------------------------------- */}

                {
                    this.get_clip_path(45)
                }

            </>
        );
    }
}

export default BarCode;