import React, { Component } from 'react';
import "../../assets/voting.css"

class Voting extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    clip_path_polygon = () => {
        return (
            <div className="row star-row">
                <div className="span-data">
                    <img className="voting-polygon-image" src="/images/arowex-light-png.png" />
                </div>
                <div className="span-data">
                    <img className="voting-polygon-image" src="/images/arowex-light-png.png" />

                </div>
                <div className="span-data">
                    <img className="voting-polygon-image" src="/images/arowex-light-png.png" />

                </div>
                <div className="span-data">
                    <img className="voting-polygon-image" src="/images/arowex-light-png.png" />

                </div>
                <div className="span-data">
                    <img className="voting-polygon-image" src="/images/arowex-light-png.png" />
                </div>
                <div className="span-data">
                    <img className="voting-polygon-image" src="/images/arowex-light-png.png" />
                </div>
            </div>
        )
    }

    card_function = () => {
        return (
            <div className="center_div2" >

                <div className="row" style={{ padding: '1rem' }}>
                    <div className="col-4">
                        <div className="header-circle1">
                            <div className="header-circle2">
                                <div className="header-circle3">
                                    <p className="para-class" style={{ color: 'white' }}>
                                        <img className="voting-logo-image" src="/images/arowex-light-png.png" />
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-5 div-section">
                        <div className="bottom_div2">
                            <h2>Current Round</h2>
                            <h2>Total</h2>
                            <h2>Air Drop</h2>
                        </div>
                    </div>
                    <div className="col-3">
                        <h3 style={{ color: 'white', color: 'cyan', fontSize: 'large' }}>VEVO (Vevo)</h3>
                    </div>
                </div>

                <div className="inside-center-div2">

                    <hr style={{ background: 'cyan', marginBottom: '5px', marginTop: '0px', height: '2px' }} />
                    <div className="row top_div2">
                        <div className="col-8">
                        </div>
                        <div className="col-4" style={{ float: 'right' }}>
                            <div className="circle">
                                <p style = {{marginTop: '20%'}}>VOTE</p>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="clip-path-design">
                    <div className="footer_div2">
                        {
                            this.clip_path_polygon()
                        }
                        {
                            this.clip_path_polygon()

                        }
                    </div>
                </div>
            </div>
        )
    }

    render() {
        return (
            <div className="voting-game">
                <div className={window.innerWidth > 772 ? 'sidebar' : 'sidebar-responsive'}>
                    <div className="clip-path1">
                        <a className="active" href="#home">Trending</a>
                    </div>
                    <div className="clip-path1">
                        <a className="active" href="#home">Top List</a>
                    </div>
                    <div className="clip-path1">
                        <a className="active" href="#home">Newly </a>
                    </div>
                    <div className="clip-path1">
                        <a className="active" href="#home">Listed</a>
                    </div>

                </div>

                <div className="content">
                    <div className="row cards-row">
                        <div className="col-lg-4">
                            {this.card_function()}
                        </div>
                        <div className="col-lg-4">
                            {this.card_function()}
                        </div>

                        <div className="col-lg-4">
                            {this.card_function()}
                        </div>
                        <div className="col-lg-4">
                            {this.card_function()}
                        </div>

                        <div className="col-lg-4">
                            {this.card_function()}
                        </div>
                        <div className="col-lg-4">
                            {this.card_function()}
                        </div>
                    </div>

                </div>

            </div>
        );
    }
}

export default Voting;