import React, { Component } from 'react';

import { table_head } from "../Content_Data/Table/table";
import Table_object from "../Table2/Table2.js";

class Tables extends Component {
    constructor(props) {
        super(props);
        this.state = {
            show_tr: 'btc',
            coins: [
                [
                    'usdt',
                    ['BTC/USDT', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                 
                ],
                [
                    'usdt',
                    ['BTC/USDT', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                 
                ],
                [
                    'usdt',
                    ['BTC/USDT', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                 
                ],
                [
                    'usdt',
                    ['BTC/USDT', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                 
                ],
                [
                    'usdt',
                    ['BTC/USDT', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                 
                ],
                [
                    'usdt',
                    ['BTC/USDT', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                 
                ],
                [
                    'btc',
                    ['BTC/JVZ', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                ],
                [
                    'btc',
                    ['BTC/JVZ', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                ],
                [
                    'btc',
                    ['BTC/JVZ', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                ],
                [
                    'btc',
                    ['BTC/JVZ', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                ],
                [
                    'btc',
                    ['BTC/JVZ', '102.43', '+2.301', '10794,1', '1033.2', '2012.43', '$3241.20']
                ]
            ]
        }
    }

    show_currency = (event) => {
        console.log('event :::::', event.target.id);
        this.setState({
            show_tr: event.target.value
        })
    }

    render() {
        const { show_tr, coins } = this.state
        return (
            <>
                <div className="row table-data" style={{ alignItems: 'center' }}>
                    <div className="col-xl-2 col-lg-2 col-md-4 col-sm-4 col-xs-4 col-4 buttons-class">
                        <button type="button" class="btn btn-primary" style={{ width: '5rem'}} value='fav' onClick={(e) => this.show_currency(e, 'value')}>FAV</button>
                    </div>
                    <div className="col-xl-2 col-lg-2 col-md-4 col-sm-4 col-xs-4 col-4 buttons-class">
                        <button type="button" class="btn btn-primary" style={{ width: '5rem' }} value='usdt' onClick={(e) => this.show_currency(e, 'value')}>USDT</button>
                    </div>

                    <div className="col-xl-2 col-lg-2 col-md-4 col-sm-4 col-xs-4 col-4 buttons-class">
                        <button type="button" class="btn btn-primary" style={{ width: '5rem' }} value='btc' onClick={(e) => this.show_currency(e, 'value')}>BTC</button>
                    </div>
                    <div className="col-xl-2 col-lg-2 col-md-6 col-sm-6 col-xs-6 col-6 buttons-class">
                        <button type="button" class="btn btn-primary" style={{ width: '5rem' }} value='eth' onClick={(e) => this.show_currency(e, 'value')}>ETH</button>
                    </div>
                    <div className="col-xl-2 col-lg-2 col-md-6 col-sm-6 col-xs-6 col-6 buttons-class">
                        <button type="button" class="btn btn-primary" style={{ width: '5rem' }} value='jvz' onClick={(e) => this.show_currency(e, 'value')}>JVZ</button>
                    </div>
                    <div className="col-xl-2 col-lg-2 col-md-2 col-sm-2 col-xs-6 col-12 buttons-class">
                        <input className="form-control mr-sm-2" style={{ textAlign: 'center' }} type="search" placeholder="Search" aria-label="Search"></input>
                    </div>
                </div>



                <div className="table-responsive" style={{ backgroundColor: 'rgb(24, 20, 75)' }}>
                    <div className="table">

                        <table className="table">
                            <thead>
                                {
                                    Table_object.th_star()
                                }
                                {
                                    table_head.map(data => {
                                        return Table_object.th(data)
                                    })
                                }
                                <tr>
                                    {
                                        Table_object.get_array(data => {
                                            return data
                                        })
                                    }
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    coins.map(data => {
                                        if (data[0] == show_tr) {
                                            return (
                                                Table_object.row_data(
                                                    data[1]
                                                )
                                            )
                                        }
                                    })

                                }

                            </tbody>
                        </table>


                    </div>
                </div>




            </>
        );
    }
}

export default Tables;